<template>
  <div class="row">
    <div>
      <el-button type="primary" @click="join">JOIN</el-button>
      <el-button type="primary" @click="leave">LEAVE</el-button>
    </div>
  </div>
</template>

<script>
import axios from 'axios'
import { useStore } from 'vuex'
import AgoraRTC from 'agora-rtc-sdk-ng'
export default {
  setup() {
    const store = useStore()
    let rtc = {
      localAudioTrack: null,
      locaVdeioTrack: null,
      client: null
    }
    let options = {
      appId: '',
      channel: '',
      token: '',
      uid: 0
    }
    async function join() {
      // Join an RTC channel.
      console.log(options)
      await rtc.client.join(
        options.appId,
        options.channel,
        options.token,
        options.uid
      )
      // Create a local audio track from the audio sampled by a microphone.
      rtc.localAudioTrack = await AgoraRTC.createMicrophoneAudioTrack()
      // Create a local video track from the video captured by a camera.
      rtc.localVideoTrack = await AgoraRTC.createCameraVideoTrack()
      // Publish the local audio and video tracks to the RTC channel.
      await rtc.client.publish([rtc.localAudioTrack, rtc.localVideoTrack])
      // Dynamically create a container in the form of a DIV element for playing the local video track.
      const localPlayerContainer = document.createElement('div')
      // Specify the ID of the DIV container. You can use the uid of the local user.
      localPlayerContainer.id = options.uid
      localPlayerContainer.textContent = 'Local user ' + options.uid
      localPlayerContainer.style.width = '640px'
      localPlayerContainer.style.height = '480px'
      document.body.append(localPlayerContainer)

      // Play the local video track.
      // Pass the DIV container and the SDK dynamically creates a player in the container for playing the local video track.
      rtc.localVideoTrack.play(localPlayerContainer)
      console.log('publish success!')
    }
    async function leave() {
      // Destroy the local audio and video tracks.
      rtc.localAudioTrack.close()
      rtc.localVideoTrack.close()

      // Traverse all remote users.
      rtc.client.remoteUsers.forEach((user) => {
        // Destroy the dynamically created DIV containers.
        const playerContainer = document.getElementById(user.uid)
        playerContainer && playerContainer.remove()
      })

      // Leave the channel.
      await rtc.client.leave()
    }
    const getMeetingInfo = async () => {
      const res = await axios({
        method: 'POST',
        url: 'http://127.0.0.1:3007/api/meetinginfo',
        params: { username: 'guoqiang' }
      })
      const data = res.data
      options.appId = data.appID
      options.uid = parseInt(data.id)
      options.channel = store.state.meeting.channelName
      options.token = store.state.meeting.mtoken
      console.log(options.appId === '5a37bf26be414c83b920c3eedc3444c9')
      console.log(options.uid === 5)
      console.log(options.channel === 'homeone')
      console.log(options.token)
      console.log(
        options.token ===
          '0065a37bf26be414c83b920c3eedc3444c9IABHz5r6kVhFHyo5TQ6epZK2cheO9M3XB7hHC9+WVdL9Rl2L4DSuK7GEIgC3egbk1L8YYgQAAQBkfBdiAgBkfBdiAwBkfBdiBABkfBdi'
      )
      // options.appId = '5a37bf26be414c83b920c3eedc3444c9'
      // options.uid = 5
      // options.channel = 'homeone'
      // options.token =
      //   '0065a37bf26be414c83b920c3eedc3444c9IABHz5r6kVhFHyo5TQ6epZK2cheO9M3XB7hHC9+WVdL9Rl2L4DSuK7GEIgC3egbk1L8YYgQAAQBkfBdiAgBkfBdiAwBkfBdiBABkfBdi'
      // console.log(options)
      console.log('options2:', options)
    }
    getMeetingInfo()
    async function startBasicCall() {
      // Create an AgoraRTCClient object.
      rtc.client = AgoraRTC.createClient({ mode: 'rtc', codec: 'vp8' })

      // Listen for the "user-published" event, from which you can get an AgoraRTCRemoteUser object.
      rtc.client.on('user-published', async (user, mediaType) => {
        // Subscribe to the remote user when the SDK triggers the "user-published" event
        await rtc.client.subscribe(user, mediaType)
        console.log('subscribe success')

        // If the remote user publishes a video track.
        if (mediaType === 'video') {
          // Get the RemoteVideoTrack object in the AgoraRTCRemoteUser object.
          const remoteVideoTrack = user.videoTrack
          // Dynamically create a container in the form of a DIV element for playing the remote video track.
          const remotePlayerContainer = document.createElement('div')
          // Specify the ID of the DIV container. You can use the uid of the remote user.
          remotePlayerContainer.id = user.uid.toString()
          remotePlayerContainer.textContent =
            'Remote user ' + user.uid.toString()
          remotePlayerContainer.style.width = '640px'
          remotePlayerContainer.style.height = '480px'
          document.body.append(remotePlayerContainer)

          // Play the remote video track.
          // Pass the DIV container and the SDK dynamically creates a player in the container for playing the remote video track.
          remoteVideoTrack.play(remotePlayerContainer)

          // Or just pass the ID of the DIV container.
          // remoteVideoTrack.play(playerContainer.id);
        }

        // If the remote user publishes an audio track.
        if (mediaType === 'audio') {
          // Get the RemoteAudioTrack object in the AgoraRTCRemoteUser object.
          const remoteAudioTrack = user.audioTrack
          // Play the remote audio track. No need to pass any DOM element.
          remoteAudioTrack.play()
        }

        // Listen for the "user-unpublished" event
        rtc.client.on('user-unpublished', (user) => {
          // Get the dynamically created DIV container.
          const remotePlayerContainer = document.getElementById(user.uid)
          // Destroy the container.
          remotePlayerContainer.remove()
        })
      })
    }
    startBasicCall()
    return {
      join,
      leave
    }
  }
}
</script>

